.footer {
  &__main {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-top: solid 1px #fff;
    border-bottom: solid 1px #fff;
    &__part {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      &__link {
        font-size: 14px;
        font-weight: 400;
        line-height: 21px;
        letter-spacing: 0em;
        text-align: left;
        color: #fff;
        text-decoration: none;

        &--main {
          text-transform: uppercase;
        }
      }
    }
  }

  &__add {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
  }
}
